import { HttpParams } from '@angular/common/http';
import { HttpService } from '@models4insight/http';
import { HttpCacheService } from 'libs/http/src/lib/http-cache.service';
import { Dictionary, isNaN, isNil } from 'lodash';
import { AtlasApiModule } from './atlas-api.module';

/**
 * Creates a new `HttpParams` object based on the given parameters, filtering out any parameter of which the value is null or undefined
 */
export function createHttpParams(
  /** The set of key value pairs to include as parameters */
  params: Dictionary<any>
): HttpParams {
  let result: HttpParams = new HttpParams();
  Object.entries(params).forEach(([key, value]) => {
    if (!isNil(value) && !isNaN(value)) {
      result = result.set(key, value);
    }
  });
  return result;
}

/**
 * Returns the `HttpService` as imported by the `RepositoryModule`. If the `RepositoryModule` has not been loaded, throws an error.
 */
export function getHttpClient(): HttpService {
  if (!AtlasApiModule.injector) {
    throw new Error(
      'Tried running an API query while the AtlasApiModule was not loaded. Please make sure the AtlasApiModule is imported.'
    );
  }
  return AtlasApiModule.injector.get(HttpService);
}

export function getHttpCache(): HttpCacheService {
  if (!AtlasApiModule.injector) {
    throw new Error(
      'Tried accessing the cache while the AtlasApiModule was not loaded. Please make sure the AtlasApiModule is imported.'
    );
  }
  return AtlasApiModule.injector.get(HttpCacheService);
}
