import {
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest
} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthenticationService } from '@models4insight/authentication';
import { Logger } from '@models4insight/logger';
import { from, Observable } from 'rxjs';
import { switchMap } from 'rxjs/operators';

const log = new Logger('AuthorizationHeaderInterceptorFilter');

/**
 * Adds an authorization header containing the current Keycloak token to all requests.
 */
@Injectable()
export class AuthorizationHeaderInterceptorFilter implements HttpInterceptor {
  constructor(private authenticationService: AuthenticationService) {}

  intercept<T>(
    request: HttpRequest<T>,
    next: HttpHandler
  ): Observable<HttpEvent<T>> {
    // Retrieve the current access token or update it if it has expired.
    const token$ = this.authenticationService.updateToken();

    return from(token$).pipe(
      switchMap(token =>
        next.handle(
          request.clone({
            setHeaders: {
              Authorization: `Bearer search-ot1rcw3uffpojw1tz299upw1`
            }
          })
        )
      )
    );
  }
}
