import { HttpClient, HttpClientModule } from '@angular/common/http';
import {
  ModuleWithProviders,
  NgModule,
  Optional,
  SkipSelf
} from '@angular/core';
import { AuthorizationHeaderInterceptor } from './authorization-header.interceptor';
import { AuthorizationHeaderInterceptorFilter } from './authorization-header-filter.interceptor';
import { CacheInterceptor } from './cache.interceptor';
import { ErrorHandlerInterceptor } from './error-handler.interceptor';
import { HttpCacheService } from './http-cache.service';
import { HttpConfig, HttpConfigService } from './http-config.service';
import { HttpService } from './http.service';
import { IECacheHeaderInterceptor } from './ie-cache-header-interceptor';

@NgModule({
  imports: [HttpClientModule]
})
export class HttpModule {
  constructor(@Optional() @SkipSelf() parentModule: HttpModule) {
    if (parentModule) {
      throw new Error(
        'HttpModule is already loaded. Import it in the AppModule only'
      );
    }
  }

  static forRoot(config: HttpConfig = {}): ModuleWithProviders<HttpModule> {
    return {
      ngModule: HttpModule,
      providers: [
        AuthorizationHeaderInterceptor,
        CacheInterceptor,
        ErrorHandlerInterceptor,
        IECacheHeaderInterceptor,
        HttpCacheService,
        AuthorizationHeaderInterceptorFilter,
        {
          provide: HttpClient,
          useClass: HttpService
        },
        {
          provide: HttpConfigService,
          useValue: config
        }
      ]
    };
  }
}
